/* Premium serif design system with elegant typography and refined color palette */

/* Premium smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Refined animations */
@keyframes elegant-float {
  0%,
  100% {
    transform: translateY(0px) scale(1);
  }
  50% {
    transform: translateY(-8px) scale(1.02);
  }
}

.elegant-float {
  animation: elegant-float 8s ease-in-out infinite;
}

/* Premium gradient backgrounds */
.bg-premium-gradient {
  background: linear-gradient(135deg, #faf8f3 0%, #f4f0e6 50%, #e8dfc7 100%);
}

.bg-gold-gradient {
  background: linear-gradient(135deg, #d4af37 0%, #b8941f 100%);
}

.bg-therapeutic-gradient {
  background: linear-gradient(135deg, #1e3a8a 0%, #1e40af 100%);
}

/* Premium text effects */
.text-gold-shimmer {
  background: linear-gradient(45deg, #d4af37, #b8941f, #d4af37);
  background-size: 200% 200%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Elegant shadows */
.shadow-premium {
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.shadow-premium-lg {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.15),
    0 20px 25px -5px rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
  background: transparent;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: transparent;
  border-radius: 6px;
  /* border: 2px solid white; */
}

/* For Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}
